@import '../../styles/variables.scss';
@import '../../styles/mixin.scss';

.chat__header {
    width: 100%;
    border-bottom: $border;
    margin-bottom: 10px;
    @include phone(){
        margin-bottom: 5px;
    }
}

.chat__header__text {
    font-size: 30px;
    color: $color-text;
    font-weight: 600;
    font-family: Segoe UI Historic, Segoe UI, Helvetica, Arial, sans-serif;
}

.chat__header__wrap {
    display: flex;
    align-items: center;
    padding: 5px 12px;
    gap: 15px;
    width: 90%;
    margin: 0 auto;
    @include laptop() {
        margin: 0 auto;
        justify-content: space-between;
        gap: 0px;
    }
    @include flexible(600px){
        width: 98%;
    }
    @include phone(){
        padding: 5px 15px;
    }
}

.chat__header__img {
    width: 55px;
    height: 55px;
    border-radius: 50%;

}
.chat__header-user-info{
    display: flex;
    align-items: center;
    gap: 10px;
}

.chat__header__arrow {
    margin-right: 5px;
    padding-top: 2px;
    fill: $color-text;
    transition: .2s;
    width: 45px;
    cursor: pointer;
    height: 45px;
    &>svg {
        width: inherit;
        height: inherit;
    }

    &:hover {
        fill: $color-orange-hover;
    }

    @include tablet() {
        margin: 0px;
    }
}