@import '../../styles/variables.scss';
@import '../../styles/mixin.scss';

.chat__body {
    height: 100%;
    width: 100%;
    position: relative;

}

.chat__body-wrap {
    position: absolute;
    bottom: 0px;
    right: 0;
    left: 0;
    display: flex;
    flex-direction: column;
    gap: 8px;
    max-width: 90%;
    max-height: 100%;
    margin: 0 auto;

    overflow-y: scroll;
    @include flexible(600px) {
        width: 98%;
    }

    -ms-overflow-style: none;
    scrollbar-width: none;
}

.chat__body-wrap::-webkit-scrollbar {
    width: 0;
    height: 0;
}

.message {
    background-color: $bg-message;
    border: $border-message;
    align-self: start;
    padding: 8px 15px;
    border-radius: 15px;
    max-width: 300px;
    word-wrap: break-word;
    flex-wrap: wrap;
    @include laptop(){
        max-width: 40%;
    }
    @include phone(){
        max-width: 46%;
    }
}

.my-message {
    align-self: flex-end;
    background-color: $bg-my-message;
}