@import '../../styles/variables.scss';
@import '../../styles/mixin.scss';
.aside__chat-wrap{
    background-color: $bg-elements;
    transition: .2s;
}

.aside__chat{
    text-decoration: none;
    &:hover .aside__chat-wrap{
        background-color: $bg-color-cream;
    }
    &.active > .aside__chat-wrap{
        background-color: $bg-color-cream;
    }
}
.aside__chat-wrap{
    border-radius: 15px;
    display: flex;
    padding: 10px 10px 10px 0px;
}
.aside__chat-img{
    width: 60px;
    height: 60px;
    & > .aside__chat-img{
        width: inherit;
        height: inherit;
        border-radius: 50%;
    }
    @include phone(){
        width: 70px;
        height: 70px;
    }
}
.aside__chat-text{
    max-width: 80%;
    padding-left: 10px;
}
.aside__chat-text-name{
    font-size: 18px;
    color: $color-text;
    font-weight: 600;
    font-family: Segoe UI Historic, Segoe UI, Helvetica, Arial, sans-serif;
    @include phone(){
        font-size: 20px;
    }
}
.aside__chat-text-last-message{
    text-overflow:ellipsis;
    padding-right: 5px;
    overflow:hidden;
    margin-top: 5px;
    font-size: 16px;
    color: $color-last-message;
    font-weight: 400;
    height: 20px;
    font-family: Segoe UI Historic, Segoe UI, Helvetica, Arial, sans-serif;
    @include phone(){
        font-size: 17px;
        height: 22px;
    }
}