@mixin laptop() {
    @media  screen and (max-width: 1024px) {
      @content;
    }
}
@mixin tablet() {
    @media  screen and (max-width: 768px) {
      @content;
    }
}
@mixin phone() {
    @media  screen and (max-width: 500px) {
      @content;
    }
}

@mixin flexible($width) {
  @media  screen and (max-width: $width) {
    @content;
  }
}