@import '../../styles/variables.scss';
@import '../../styles/mixin.scss';
.login__container {
    width: 100%;
    max-width: 400px;
    margin: 80px auto 0px auto;
    @include phone(){
        margin-top: 15px;
    }
}

.login {
    padding: 10px 20px;
}

.logo {
    color: $color-blue;
    font-size: 50px;
    text-align: center;
    font-weight: 700;
    font-family: Helvetica, Arial, sans-serif;
}

.form {
    text-align: center;
    padding: 20px 10px;
    border-radius: 5px;
    box-shadow: $box-shadow-form;
    border: $border;
    background-color: $bg-form;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 15px;
}

.form-wrap {
    margin-top: 10px;
}

.from__text {
    color: $color-text;
    font-size: 18px;
    line-height: 22px;
    font-weight: 400;
}

.form__element {
    width: 95%;
    margin: 0 auto;
}

.form__input {
    background-color: $bg-color-cream;
    color: $color-text;
    border-radius: 6px;
    font-size: 17px;
    padding: 14px 16px;
    width: 100%;
    border: $border;
    outline: none;
    &::placeholder{
        opacity: .6;
        color: $color-text;
    }
}
.form__btn{
    width: 100%;
    color: $color-white;
    background-color: $color-blue;
    border: 0px;
    font-size: 20px;
    font-weight: 700;
    padding: 14px 16px;
    border-radius: 5px;
    &:hover{
        background-color: $color-blue-dark;
    }
}

.or{
    position: relative;
    &__text{
        font-size: 13px;
        color: #aaa;
        &::after{
            display: block;
            content: '';
            position: absolute;
            left: 0px;
            width: 43%;
            height: 1px;
            background-color: rgba(170, 170, 170, 0.5);
            top: 10px;
        }
        &::before{
            display: block;
            content: '';
            width: 43%;
            height: 1px;
            background-color: rgba(170, 170, 170, 0.5);
            position: absolute;
            top: 10px;
            right: 0px;
        }
    }
}