@import '../../styles/variables.scss';
.theme__switch{
    text-align: left;
    margin-left: 10px;
}
.setting__text{
    padding: 20px 0px 15px 10px;
    font-size: 15px;
    color: rgb(146, 145, 145);
}



.switch {
    position: relative;
    display: inline-block;
    width: 90px;
    height: 34px;
}


.switch input {
    opacity: 0;
    width: 0;
    height: 0;
}

.slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgb(146, 145, 145);
    transition: 0.4s;
    transition: 0.4s;
}

.slider:before {
    position: absolute;
    content: "";
    height: 40px;
    width: 40px;
    left: 0px;
    bottom: 4px;
    top: 0;
    bottom: 0;
    margin: auto 0;
    transition: 0.4s;
    transition: 0.4s;
    box-shadow: 0 0px 15px #2020203d;
    background: white url('https://i.ibb.co/FxzBYR9/night.png');
    background-repeat: no-repeat;
    background-position: center;
}

input:checked+.slider {
    background-color: $color-blue;
}

input:focus+.slider {
    box-shadow: 0 0 1px $color-blue;
}

input:checked+.slider:before {
    transform: translateX(54px);
    transform: translateX(54px);
    transform: translateX(54px);
    background: white url('https://i.ibb.co/7JfqXxB/sunny.png');
    background-repeat: no-repeat;
    background-position: center;
}

/* Rounded sliders */
.slider.round {
    border-radius: 34px;
}

.slider.round:before {
    border-radius: 50%;
}