@import '../../styles/variables.scss';
@import '../../styles/mixin.scss';

.aside {
    width: 100%;
    max-width: 300px;
    min-width: 300px;
    background-color: $bg-elements;
    border-right: $border;
    overflow: hidden;
    @include phone(){
        padding: 0px 5px;
        max-width: none;
        border: none;
    }
    @include flexible(420px){
        padding: 0px;
    }
}

.aside__header {
    width: 100%;
}

.aside__header-setting {
    margin-top: 5px;
    padding: 10px 15px;
    display: flex;
    justify-content: space-between;
}

.aside__header-setting-img {
    width: 32px;
    height: 32px;
    transition: .2s;
    cursor: pointer;
    fill: $color-setting-img;

    &:hover {
        fill: $color-orange-hover
    }
}

.aside__header-setting-text {
    font-size: 28px;
    font-weight: 700;
}

.aside__header-setting-link {
    color: $color-text;
    text-decoration: none;
    transition: .2s;

    &:hover {
        color: $color-orange-hover;
    }
}

.no-chats-text {
    padding-left: 10px;
    line-height: 25px;
    text-align: left;
    font-size: 20px;
    color: $color-text;
}

.search {
    padding: 0px 10px;
    margin-top: 8px;
}

.search__input {
    border-radius: 30px;
    padding: 10px 15px 10px 15px;
    font-size: 16px;
    color: $color-text;
    background-color: $bg-color-cream;
    width: 100%;
    outline: $aside-input-outline;
    transition: .2s;
    border: none;

    &:focus {
        outline: $aside-input-outline-focuse;
    }
}

.aside__chats {
    width: 95%;
    height: 85%;
    overflow-y: scroll;
    margin: 15px auto 0px auto;
    -ms-overflow-style: none;
    scrollbar-width: none;
}
.aside__chats::-webkit-scrollbar {
    width: 0;
    height: 0;
}
.searct-text {
    color: $color-text;
    display: block;
    font-size: 18px;
    overflow: hidden;
    white-space: nowrap;
    max-width: 100%;
    text-overflow: ellipsis;
    font-weight: 400;
    padding: 5px 10px;
}

.searct-text__value {
    font-size: 19px;
}