html[data-theme="dark"] {
    --color-blue: #1876f2c4;
    --color-blue-dark: #006efffd;
    --color-orange-hover: rgb(255, 70, 70);
    --color-green: #42b72a;
    --color-white: #fff;
    --color-link-form: rgb(22, 99, 206);
    --bg-elements: black;
    --aside-input-outline: 2px solid rgba(255, 0, 0, 0.7);
    --aside-input-outline-focus: 2px solid rgba(255, 0, 0, 1);
    --color-text: rgb(255, 255, 255);
    --color-bg-root: rgb(0, 0, 0);
    --color-last-message: rgb(155, 155, 155);
    --bg-color-cream: rgba(134, 142, 153, 0.1);
    --border: 1px solid #7776764f;
    --box-shadow-form: 0px 1px 1px rgb(255 ,255, 255 , .1), 0px 1px 1px rgb(255 ,255, 255 , .1);
    --bg-from: rgba(0, 0, 0, 1);
    --color-setting-img: rgb(255, 255, 255);
    --color-bg-chat: rgb(0, 0, 0);
    --bg-message: rgb(33,33,33,.9);
    --bg-my-message: rgb(177, 55, 214);
    --border-message: 1px solid rgb(38, 38, 38);

}

html[data-theme="light"] {
    --border-message: 1px solid rgba(170, 170, 170, 0.25);
    --color-text: rgb(0, 0, 0);
    --bg-my-message: rgb(239, 239, 239);
    --bg-message: rgb(255, 255, 255);
    --box-shadow-form: 0px 2px 4px rgb(0 0 0 / 10%), 0px 8px 16px rgb(0 0 0 / 10%);
    --color-blue: #1877f2;
    --color-link-form: rgb(85, 26, 139);
    --color-blue-dark: #006efffd;
    --color-orange-hover: rgb(247, 146, 63);
    --color-green: #42b72a;
    --color-form-text: rgb(0, 0, 0);
    --color-white: #fff;
    --bg-elements: #fff;
    --bg-from: #fff;
    --aside-input-outline: 1px solid rgba(255, 0, 0, .5);
    --aside-input-outline-focus: 1px solid rgba(255, 0, 0, 1);
    --color-aside-text: rgb(0, 0, 0);
    --color-bg-root: #f0f2f5;
    --color-last-message: rgb(90, 90, 90);
    --bg-color-cream: rgba(134, 142, 153, 0.1);
    --border: 1px solid #dddfe2;
    --color-setting-img: #000;
}
$border-message: var(--border-message);
$color-blue: var(--color-blue);
$bg-my-message: var(--bg-my-message);
$box-shadow-form: var(--box-shadow-form);
$aside-input-outline-focuse: var(--aside-input-outline-focus);
$aside-input-outline: var(--aside-input-outline);
$color-setting-img: var(--color-setting-img);
$aside-shadow: var(--aside-shadow);
$bg-elements: var(--bg-elements);
$color-link-form: var(--color-link-form);
$bg-form: var(--bg-from);
$bg-message: var(--bg-message);
$bg-aside-header: var(--bg-aside-header);
$color-blue-dark: var(--color-blue-dark);
$color-orange-hover: var(--color-orange-hover);
$color-green: var(--color-green);
$color-white: var(--color-white);
$color-text: var(--color-text);
$color-bg-root: var(--color-bg-root);
$color-last-message: var(--color-last-message);
$bg-color-cream: var(--bg-color-cream);
$border: var(--border);